@import url(https://fonts.googleapis.com/css?family=Indie+Flower&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center; }

.App-logo {
  height: 40vmin; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #09d3ac; }

.add {
  position: fixed !important;
  right: 30px;
  bottom: 40px; }

.coffee-card {
  flex: 1 0 21%;
  margin: 7px;
  position: relative;
  min-width: 275; }
  .coffee-card .coffee-img {
    width: 100%;
    border-radius: 5px;
    height: 70px;
    padding-top: 56.25%; }
  .coffee-card .icon {
    position: absolute;
    top: 1.5vw;
    color: white;
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.4);
    border-radius: 50%;
    padding: 5px; }
  .coffee-card .icon:hover {
    color: red; }
  .coffee-card .delete {
    right: 10px; }
  .coffee-card .edit {
    right: 50px; }
  .coffee-card .container {
    display: flex;
    justify-content: space-between;
    align-items: baseline; }
    .coffee-card .container .title {
      flex: 1 1;
      font-size: 20px; }

.coffee-card:hover .coffee-img {
  opacity: 0.8; }

.header {
  background-color: grey;
  margin-bottom: 20px;
  padding: 10px;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .header .logo-container {
    text-decoration: none; }
    .header .logo-container .logo-text {
      flex: 1 1;
      font-family: 'Indie Flower', cursive;
      font-size: 28px;
      margin-left: 15px;
      color: white;
      margin: 0 10px; }
      @media screen and (max-width: 450px) {
        .header .logo-container .logo-text {
          font-size: 22px; } }
    .header .logo-container .logo-img {
      vertical-align: sub; }
      @media screen and (max-width: 450px) {
        .header .logo-container .logo-img {
          vertical-align: bottom; } }
  .header .coffee-count {
    color: white; }
    @media screen and (max-width: 450px) {
      .header .coffee-count {
        font-size: 15px; } }

